/**
 * Template: Cart
 * ------------------------------------------------------------------------------
 * Loosely coupled cart page scripts.
 *
 * @namespace cart
 */
import { decodeHTMLEntities } from '~/helpers/utils';
import CartAddOn from '~/components/cart-add-on';
import CartTable from '~/components/cart-table';

/**
 * DOM selectors.
 */
const selectors = {
  container: '[js-cart="container"]',
  table: '[js-cart-table="container"]',
  panel: '[js-cart="panel"]'
};

/**
 * DOM node selectors.
 */
const nodeSelectors = {
  container: document.querySelector(selectors.container),
  panel: document.querySelector(selectors.panel)
};
document.addEventListener('DOMContentLoaded', () => {
  if (!nodeSelectors.panel) {
    return;
  }

  /**
   * Render cart table.
   */
  CartTable(selectors.table).init();
  if (theme.features.cartAddOn && theme.features.cartAddOnProduct) {
    CartAddOn().init();
  }

  /**
   * Set EventBus event listeners.
   */
  Frame.EventBus.listen('AjaxCart:cartEmpty', () => {
    const strings = {
      title: decodeHTMLEntities(theme.strings.cart.general.title),
      empty: decodeHTMLEntities(theme.strings.cart.general.empty),
      continueBrowsing: decodeHTMLEntities(theme.strings.cart.general.continue_browsing)
    };
    nodeSelectors.container.innerHTML = "\n      <div class=\"container\">\n        <div class=\"row\">\n          <div class=\"col xs12 m6 offset-m3 text-center\">\n            <h1 class=\"template-cart__empty-title\">".concat(strings.title, "</h1>\n\n            <p class=\"template-cart__empty-text\">\n              ").concat(strings.empty, "\n            </p>\n\n            <a\n              class=\"\n                template-cart__empty-button\n                button\n                button--primary button--dark\n                button--large\n              \"\n              href=\"/collections/all\"\n            >\n              ").concat(strings.continueBrowsing, "\n            </a>\n          </div>\n        </div>\n      </div>\n    ");
  });
});